import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			versionName:'v1.0.0',
			infofilled: {
					fontclass:'text-icons',
					color: '#ff80ab',
					size: '22',
					type: 'guanyu'
				},
			download:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'gengxin'
			},
			share:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'fenxiang'
			},
			help:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'bangzhu'
			},
			chat:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'lianxikefu'
			},
			zuji:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'zuji'
			},
			setting:{
				fontclass:'text-icons',
				color: '#ff80ab',
				size: '22',
				type: 'shezhi'
			}
		};
	},
	onLoad() {
		this.versionName = '版本:'+this.$config.versionName
		
	},
	methods: {
		goLogin() {
			uni.navigateTo({
				url: '/pages/center/login'
			});
		},
		goRegister() {
			uni.navigateTo({
				url: '/pages/center/register'
			});
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		},
		click(index){
			console.log(index);
			switch (index){
				case 0:
					uni.navigateTo({
						url: '/pages/center/footprint'
					});
					break;
				case 1:
					uni.showModal({
						title:"联系客服",
						content:"QQ群:200910943"
					})
					break;
				case 4:
					this.checkVersion().catch(()=>{
						uni.showToast({
							title: '已经是最新',
							duration:1000
						});
					})
					break;
				case 5:
					uni.navigateTo({
						url:'/pages/center/about'
					})
					break
				default:
					break;
			}
		}
	},

	/**
	 *  点击导航栏 buttons 时触发
	 */
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url:'/pages/center/setting'
		})
		
	}
};
